r"},[_c('div',{staticClass:"col-auto mb-3"},[_c('h3',{staticClass:"mb-0 fnd-2-1 fnd-2"},[_vm._v("Mecanismos de absorción")])])]),_c('p',[_vm._v("Se conocen tres (3) mecanismos para explicar la absorción: ")])]),_c('div',{staticClass:"col-lg-3 col-md-6 order-1 order-lg-2"},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/img-23.png")}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"col-xl-4 col-lg-7 col-md-9 col-11 mb-4 mb-xl-0"},[_c('div',{staticClass:"crd_hover_txt",attrs:{"data-aos":"flip-left"}},[_c('div',{staticClass:"crd_hover_txt--img"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/img-24.png"),"alt":"alt"}})])]),_c('div',{staticClass:"crd_hover_txt--body"},[_c('h6',{staticClass:"mb-3"},[_vm._v("Difusión simple a través de los poros o canales")]),_c('p',{staticClass:"mb-0"},[_vm._v("Se realiza cuando las sustancias de muy bajo peso molecular como el agua se pueden mover libremente a través de la membrana, desde el lado de alta concentración hasta el lado de baja concentración. Este proceso es fundamental para el transporte de moléculas pequeñas por la membrana celular y además es uno de los principales mecanismos de la regulación osmótica de las células.")])])])]),_c('div',{staticClass:"col-xl-4 col-lg-7 col-md-9 col-11 mb-4 mb-xl-0"},[_c('div',{staticClass:"crd_hover_txt",attrs:{"data-aos":"flip-left"}},[_c('div',{staticClass:"crd_hover_txt--img"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/img-25.png"),"alt":"alt"}})])]),_c('div',{staticClass:"crd_hover_txt--body"},[_c('h6',{staticClass:"mb-3"},[_vm._v("Difusión pasiva facilitada por transportadores")]),_c('p',{staticClass:"mb-0"},[_vm._v("Los nutrientes que son solubles en agua no pueden penetrar la membrana de la célula que es rica en lípidos, entonces en es